#btnPointer{
    cursor:text;
}

div{
    /* font-family: 'Playfair Display', serif; */
    font-family: 'Libre Baskerville', serif;
}

.social-icons:hover{
    color: rgb(22, 22, 22);
}

.navbar-custom-color{
    /* background-color: #23A0EE; */
